import { USER_LOGIN_FAILED, USER_REGISTER_SUCCESS } from '../actions/users'

export default function(state = null, action) {
  switch (action.type) {
    case USER_LOGIN_FAILED:
      return {
        error: action.payload
      }
    case USER_REGISTER_SUCCESS:
      return null
    default:
      return state
  }
}



